.txnHash,
.timeStamp {
  font-size: 14px;
  margin-left: 30px;
  margin-bottom: 4px;
}
.header_nav,
.header_nav:hover {
  text-decoration: none;
  font-size: 24px;
  color: #000;
}
.header_nav i {
  margin-right: 15px;
  color: #d4d1d1;
}

table {
  display: none;
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 48%;
  margin: 5% 1% 2%;
  float: left;
}
table.active {
  display: table;
}
/* table:nth-child(odd) {
    margin-right: 2%;
} */

td,
th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
  width: 50%;
}

tr td:nth-child(odd) {
  color: #9a9696;
}
/* tr td:nth-child(even) {
    font-weight: bold;
} */
.pagination {
  list-style: none;
  float: right;
  margin-right: 2%;
}
.pagination .page {
  display: inline-block;
  padding: 4px 15px;
  border: 1px solid #efefef;
  cursor: pointer;
}
.page.active {
  background-color: #efefef;
}
.txnDetails.active {
  position: absolute;
  top: -17px;
  display: block;
}
.txnDetails {
  display: none;
  width: 100%;
}

.version {
  padding: 5px 10px;
  background-color: #08384b;
  width: fit-content;
  border-radius: 5px;
  color: #ffffff;
  margin-right: 36px;
  margin-bottom: 4px;
}
